<template>
<div class="register">
  <div class="container-fluid vf-form-wallpaper">
    <b-row>
      <div class="col-md-6 vf-form ml-auto shadow-lg">
        <p class="h1 text-center m-5">Create your account</p>
        <b-form @submit.prevent="register">
            <b-alert v-if="errors.email" show variant="danger" class="fade-in">{{errors.email.msg}}</b-alert>
          <b-form-group label-for="email" label="Email">
            <b-input-group>
              <b-input-group-prepend is-text>
                <b-icon icon="envelope"></b-icon>
              </b-input-group-prepend>
              <b-form-input
                type="email"
                id="email"
                :placeholder="user.email"
                v-model="user.email"
              ></b-form-input>
            </b-input-group>
          </b-form-group>
          
          <b-alert v-if="errors.password" show variant="danger" class="fade-in">{{errors.password.msg}}</b-alert>
          <b-form-group label-for="password" label="Password">
            <b-input-group>
              <b-input-group-prepend is-text>
                <b-icon icon="person-fill"></b-icon>
              </b-input-group-prepend>
             
              <b-form-input
                type="password"
                id="password"
                :placeholder="user.password"
                v-model="user.password"
              ></b-form-input>
            </b-input-group>
          </b-form-group>
          <b-form-group>
            <b-button type="submit" variant="secondary btn-block" class="mt-3 app-button"
              >Create account</b-button
            >
          </b-form-group>
        </b-form>
      </div>
    </b-row>
  </div>
  </div>
</template>

<script>
const api_url = process.env.VUE_APP_API_URL;

export default {
  name: "Register",
  data() {
    return {
    errors:'',
    user: { email: "", password: "", rememberMe: false },
    };
  },
  methods: {
    register() {
      this.axios.post(`${api_url}/register`, this.user).then((response) => {
        let token = response.data.token;
          this.$store
          .dispatch('login', token)
          .then(() => {
            this.$router.push({ path: "/" });
          })
          .catch(error => {
            console.log(error);
          });
      }).catch((error)=>{
        console.log(error);
        this.errors = error.response.data.errors;
      });
    },
  },
};
</script>

<style scoped>
.container-fluid {
  height: 100vh;
  position: fixed;
}

.vf-form {
  background-color: #3b2e40d3;
  color: white;
  height: 100vh;
}

.vf-form-wallpaper {
  background-image: url("../assets/imgs/vfilm-login-register-wallpaper-rd.jpg");
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}

input:focus {
  outline: #f73859 !important;
  box-shadow: 1px 1px 5px #f73859;
  color: #393e46;
  border-color: #f73859;
}

.input-group svg {
  color: #fff;
}

.h1{
    text-shadow: 1px 2px 4px black;
}

.fade-in {
  animation: fadeIn ease 1s;
}



@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}


@-o-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
}
}
</style>
